import { __assign } from "tslib";
import { registerShape } from '../base';
import { getStyle } from '../util/get-style';
import { getRectPath } from './util';
/** 描边柱状图 */

registerShape('interval', 'hollow-rect', {
  draw: function draw(cfg, container) {
    var style = getStyle(cfg, true, false);
    var path = this.parsePath(getRectPath(cfg.points));
    var shape = container.addShape('path', {
      attrs: __assign(__assign({}, style), {
        path: path
      }),
      name: 'interval'
    });
    return shape;
  },
  getMarker: function getMarker(markerCfg) {
    var color = markerCfg.color,
        isInPolar = markerCfg.isInPolar;

    if (isInPolar) {
      return {
        symbol: 'circle',
        style: {
          r: 4.5,
          stroke: color,
          fill: null
        }
      };
    }

    return {
      symbol: 'square',
      style: {
        r: 4,
        stroke: color,
        fill: null
      }
    };
  }
});